<template>
  <div class="app-container">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="searchList" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 40px">
        <el-form :inline="true" :model="list" size="small" label-width="140px">
          <el-form-item label="商品名称：">
            <el-input style="width: 203px" v-model="list.goodName" clearable placeholder="商品名称"></el-input>
          </el-form-item>
          <el-form-item label="商品编码：">
            <el-input style="width: 203px" v-model="list.goodId" clearable placeholder="商品编码"></el-input>
          </el-form-item>
          <el-form-item label="规格编码：">
            <el-input style="width: 203px" v-model="list.goodCode" clearable placeholder="规格编码"></el-input>
          </el-form-item>
          <el-form-item label="统计时间：">
            <el-date-picker v-model="list.time" value-format="yyyy-MM-dd HH-mm-ss" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card class="operate-container" style="margin-top:10px" shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
    </el-card>
    <el-card class="operate-container" style="margin-top:10px">
      <el-table :data="tableData" style="width: 100%" border>
        <el-table-column type="index" width="50" align="center">
        </el-table-column>
        <el-table-column label="平台分类" width="100" align="center" prop="categoryName3"></el-table-column>
        <el-table-column label="商品名称" min-width="200" show-overflow-tooltip prop="name" align="center"></el-table-column>
        <el-table-column label="商品编码" width="100" align="center">
          <template slot-scope="scope">{{scope.row.productCode ? scope.row.productCode : '-'}}</template>
        </el-table-column>
        <el-table-column label="规格编码" width="140" align="center">
          <template slot-scope="scope">{{scope.row.skuCode ? scope.row.skuCode : '-'}}</template>
        </el-table-column>
        <el-table-column label="出库数量" width="150" align="center" prop="outNum"></el-table-column>
        <el-table-column label="入库数量" width="150" prop="inNum" align="center"></el-table-column>
        <el-table-column label="交易笔数" width="180" prop="saleNum" align="center">
        </el-table-column>
        <el-table-column label="操作" fixed="right" width="130" align="center">
          <template slot-scope="scope">
            <el-button @click="showProduct(scope.row)" type='text'>查看明细</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 固定在下面 -->
    <div class="fixedB">
      <!-- 页码 -->
      <el-pagination 
        background
        style="margin-top: 10px;margin-left:20px"
        @size-change="handleSizeChange"
        @current-change="pageChange"
        layout="total,sizes,prev,pager,next,jumper"
        :page-size="page_size"
        :page-sizes="[15, 30, 50, 100]"
        :current-page="pageNum"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import { getStockDetails } from '@/api/productInventory/productInventory'

export default {
  data() {
    return {
      list: {
        goodName: '',
        goodId: '',
        goodCode: '',
        time: []
      },
      tableData: [],
      pageNum: 1,
      page_size: 15,
      total: 0,

    }
  },
  created() {
    this.searchList()
  },
  methods: {
    searchList() {
      getStockDetails({
        shopId: localStorage.getItem("shopId"),
        name: this.list.goodName,
        productCode: this.list.goodId,
        skuCode: this.list.goodCode,
        startCreateTime: this.list.time == null ? '' : this.list.time[0],
        endCreateTime: this.list.time == null ? '' : this.list.time[1],
        pageSize: this.page_size,
        pageNum: this.pageNum
      }).then(res => {
        this.tableData = res.data.list
        this.total = res.data.total
      })
    },
    // 重置
    reset() {
      this.list = {
        goodName: '',
        goodId: '',
        goodCode: '',
        time: []
      }
      this.searchList()
    },

    //修改每页页数
    handleSizeChange(page) {
      this.page_size = page
      this.searchList()
    },
    //点击跳转页码
    pageChange(page) {
      this.pageNum = page
      this.searchList()
    },
    //查看详情
    showProduct(row) {
      this.$router.push({
        path: '/inventoryManagement/inAndOutInventory/detailsMain',
        query: {
          shopId: localStorage.getItem("shopId"),
          productId: row.productId,
          skuId: row.skuId
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.app-container {
  padding: 20px 20px 40px 20px;
}
</style>